@import (less) "color.less";

/*@green: #258900;*/

:host::ng-deep .form-inline {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    flex-flow: row wrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    font-size: 1em;
}

:host::ng-deep .form-inline .form-group {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    -ms-flex-flow: row wrap;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    flex-flow: row wrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    margin-bottom: 0;
}

:host::ng-deep .input-group {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}


:host::ng-deep .input-group .form-control,
:host::ng-deep .input-group-addon,
:host::ng-deep .input-group-btn {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

:host::ng-deep .form-control {
    display: block;
    width: 100%;
    /*padding: .5em .75em;*/
    /*font-size: 1em;*/
    /*line-height: 1.25;*/
    height: 3.125em;
    color: #464a4c;
    background-color: #fff;
    background-image: none;
    background-clip: padding-box;
    border-radius: .15em !important;
    transition: none !important;
    /*no-shadow*/
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    border: 1px solid @darkest_gray !important;

    &:focus:not([disabled]) , &:active:not([disabled]){
      border: 1px solid @green !important;
    }

    &[disabled]{
      border: 1px solid @light_gray !important;
      color: @light_gray;
      user-select: none;
    }
}

:host(.ng-dirty.ng-invalid.nbp-show-validation)::ng-deep .form-control:not([disabled]){
  border: 1px solid @red !important;
}

:host::ng-deep .input-group-addon {
    padding: .5em .75em;
    margin-bottom: 0;
    font-size: 1em;
    font-weight: 400;
    line-height: 1.25;
    color: @green;
    text-align: center;
    background-color: @white !important;
    border-top-color: rgba(0, 0, 0, 0.15);
    border-top-style: solid;
    border-top-width: 1px;
    border-right-color: rgba(0, 0, 0, 0.15);
    border-right-style: solid;
    border-right-width: 1px;
    border-bottom-color: rgba(0, 0, 0, 0.15);
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-left-style: none !important;
    border-left-width: 0px !important;
    border-left-color: none !important;
}

:host::ng-deep .input-group-addon img {
    width: 1.2em;
    height: 1em;
    cursor: pointer;
}

:host::ng-deep .form-control+.input-group-addon:not(:first-child) {
    border-left: 0;
}

 :host::ng-deep .button-calendar {
    background-color: white;
    border-bottom: 1px solid lightgrey;
    border-top: 1px solid lightgrey;
    border-right: 1px solid lightgrey;
    border-left: none;
    color: @green;
    /*sposta il button sopra input*/
    right: 21px;
    position: relative;
    z-index: 2;
    margin-right: -21px;
}


/*Calendar*/

:host::ng-deep .dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    min-width: 10em;
    padding: .5em 0;
    margin: .125em 0 0;
    font-size: 1em;
    color:@light_black; /*#292b2c;*/
    text-align: left;
    list-style: none;
    background-color:@white;
    background-clip: padding-box;
    /*border: 1px solid rgba(0, 0, 0, .15);*/
    /*border-radius: .25em;*/
    padding: 0px;
    top: 24px;
    left: 0px;
    border-radius: 0 !important;
    border: none !important;
}

:host::ng-deep .d-inline-block {
    display: inline-block!important;
}

:host::ng-deep .rounded {
    border-radius: .25em;
}


/*header*/

:host::ng-deep .ngb-dp-header {
    height: 4em!important;
    margin-bottom: -1.5em!important;
    border-bottom: none !important;
}

:host::ng-deep .pt-1 {
    padding-top: 0!important;
    height: 4.25em;
    margin-bottom: -2em;
}

:host::ng-deep .rounded-top {
    border-top-right-radius: .25em;
    border-top-left-radius: .25em;
    border-radius: 0 !important;
}

:host::ng-deep .bg-faded {
    background-color: white !important;
}


/*<ngb-datepicker-navigation>*/

:host::ng-deep ngb-datepicker-navigation {
    height: 2em;
    line-height: 1.85em;
}

:host::ng-deep .justify-content-between {
    -webkit-box-pack: justify!important;
    -ms-flex-pack: justify!important;
    justify-content: space-between!important;
}

:host::ng-deep .d-flex {
    display: -webkit-box!important;
    display: -ms-flexbox!important;
    display: flex!important;
}


/*btn nbp-datepicker-navigation*/

:host::ng-deep .btn-link {
    cursor: pointer;
    outline: lightgrey solid 1px !important;
}

:host::ng-deep .btn-link,
:host::ng-deep .btn-link:active,
:host::ng-deep .btn-link:focus {
    border-color: transparent;
}

:host::ng-deep .btn-link,
:host::ng-deep .btn-link.active,
:host::ng-deep .btn-link:active,
:host::ng-deep .btn-link:disabled {
    background-color: transparent;
}

:host::ng-deep .btn-link {
    font-weight: 400;
    color: @green;
    border-radius: 0;
}

:host::ng-deep button,
:host::ng-deep input,
:host::ng-deep select,
:host::ng-deep textarea {
    line-height: inherit;
}

:host::ng-deep [role=button],
:host::ng-deep a,
:host::ng-deep area,
:host::ng-deep button,
:host::ng-deep input,
:host::ng-deep label,
:host::ng-deep select,
:host::ng-deep summary,
:host::ng-deep textarea {
    -ms-touch-action: manipulation;
    touch-action: manipulation;
}

:host::ng-deep button,
{
    -webkit-appearance: button;
}

:host::ng-deep button,
:host::ng-deep select {
    text-transform: none;
}

:host::ng-deep button,
:host::ng-deep input {
    overflow: visible;
}

:host::ng-deep button,
:host::ng-deep input,
:host::ng-deep optgroup,
:host::ng-deep select,
:host::ng-deep textarea {
    font-family: sans-serif;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}

:host::ng-deep .ngb-dp-navigation-chevron::before {
    border-style: solid;
    border-width: 0.1em 0.1em 0 0 !important;
    content: '';
    display: inline-block;
    height: 0.75em !important;
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    width: 0.75em !important;
    margin: 0 0 0 0.5em !important;
    color: grey;
}

:host::ng-deep .ngb-dp-navigation-chevron.right:before {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    margin: 0 0.5em 0 0 !important;
}

:host::ng-deep .d-block {
    display: block!important;
}


/* mounth and Year*/

:host::ng-deep ngb-datepicker-navigation-select.d-block {
    width: 9em !important;
}

:host::ng-deep select {
    padding: 0.25em 0.5em;
    font-size: 0.875em;
    line-height: 1.25;
    height: inherit;
    width: 50%;
}

:host::ng-deep .d-inline-block {
    display: inline-block!important;
}

:host::ng-deep select.custom-select.d-inline-block {
    padding: 0.25em 0.5rem !important;
    font-size: 0.875em !important;
    line-height: 1.25;
    height: inherit;
    width: 50%;
}

:host::ng-deep .custom-select {
    display: inline-block;
    max-width: 100%;
    height: calc(2.25em + 2px);
    padding: .375em 1.75em .375em .75em;
    line-height: 1.25;
    color: #464a4c;
    vertical-align: middle;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: .25em;
    -moz-appearance: none;
    -webkit-appearance: none;
}


/* calendar*/

:host::ng-deep .px-1 {
    /* padding-right: .25em!important;
    padding-left: .25em!important; */
    padding-left: 0px !important;
    padding-right: 0px !important;
}

:host::ng-deep .pb-1 {
    padding-bottom: .25em!important;
}

:host::ng-deep .ngb-dp-month:first-child {
    margin-left: 0 !important;
}

:host::ng-deep .ngb-dp-month {
    pointer-events: none;
}

:host::ng-deep .ml-3 {
    margin-left: 1em!important;
}

 :host::ng-deep .ngb-dp-day,
 :host::ng-deep .ngb-dp-weekday,
 :host::ng-deep .ngb-dp-week-number {
    width: 2em !important;
    height: 2em !important;
}

:host::ng-deep .ngb-dp-day {
    position: relative;
    box-sizing: border-box;
    /*border: 1px solid #258900;*/
}

:host::ng-deep .ngb-dp-week.d-flex:not(:first-child) {
    border-top: .5px solid lightgray;
    border-bottom: .5px solid lightgray;
    border-right: 1px solid lightgray;
    border-left: 1px solid lightgray;
}

:host::ng-deep .ngb-dp-week.d-flex:first-child {
    border-top: 1px solid lightgray;
    border-right: 1px solid lightgray;
    border-bottom: .5px solid lightgray;
    border-left: 1px solid lightgray;
}

:host::ng-deep ngb-datepicker-navigation.d-flex {
    height: 2.5em!important;
    line-height: 1.8em!important;
    border-top: 1px solid lightgray;
    border-right: 1px solid lightgray;
    border-left: 1px solid lightgray;
}

:host::ng-deep .ngb-dp-weekday,
:host::ng-deep .ngb-dp-week-number {
    line-height: 2em !important;
}

:host::ng-deep .text-info {
    color: #5bc0de!important;
}

:host::ng-deep .font-italic {
    font-style: italic;
}

:host::ng-deep .outside {
    opacity: 0.5;
}

:host::ng-deep ngb-dp-day>div {
    text-align: center;
    width: 2em;
    height: 2em;
    line-height: 2em;
    /*border-radius: 0.25em;*/
    border-radius: 0px !important;
}

:host::ng-deep div[ngbdatepickerdayview] {
    border-radius: 0px !important;
    box-sizing: border-box;
    text-align: center;
    width: inherit !important;
    height: inherit !important;
    line-height: 2.6em !important;
    vertical-align: middle;
}

:host::ng-deep .text-muted {
    color: #636c72!important;
}

:host::ng-deep .btn-secondary {
    color: @green;
    background-color: #fff;
    border-color: #ccc;
    font-weight: bold;
    font-size: 0.8em;
    /* border-right: 1px solid white; */
}

:host::ng-deep .text-white {
    color: #fff!important;
}

:host::ng-deep .bg-primary {
    background-color: @green !important;
}

:host::ng-deep .bg-primary::before {
    border-bottom: 8px solid white;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    position: absolute;
    top: 0px;
    right: -8px;
    content: '';
    display: inline-block;
    width: 0px;
    height: 0px;
    margin: 0;
    padding: 0;
    transform: rotate(45deg);
    transform-origin: top;
}

:host::ng-deep .text-center {
    text-align: center!important;
}

:host::ng-deep .small,
:host::ng-deep small {
    font-size: 80%;
    font-weight: 400;
}

:host::ng-deep .ngb-dp-months.pb-1 {
    padding: 0px !important;
}

:host::ng-deep .input-group>input.form-control {
    padding-left: .625em;
    padding-right:2.5em;
}

:host::ng-deep .ngb-dp-day:not(:first-child)>.btn-secondary {
    border-left: 1px solid lightgray;
}

:host::ng-deep .ngb-dp-weekday.font-italic {
    font-family: 'Arial Bold', 'Arial';
    font-weight: 700;
    font-style: normal !important;
    font-size: .75em;
    color: @gray !important;
    text-align: center;
    text-transform: uppercase;
}

:host::ng-deep button.btn-link {
    color: @green !important;
}

 :host::ng-deep .collapsed {
    margin-bottom: -2em !important;
}

 :host::ng-deep .ngb-dp-month-name {
    font-size: .8em !important;
    padding-top: 1em !important;
    font-weight: 700;
    text-transform: uppercase;
    color: @green;
    position: absolute;
    text-align: center !important;
    top: 0;
    left: 3.1em !important;
    height: 3.2em !important;
    width: 11.4em;
    line-height: 1.42857143em !important;
    word-wrap: break-word;
    word-break: break-word;
}


 :host::ng-deep .ngb-dp-weekday {
    width: 2.666666666666667em !important;
}
