.button-icon{
  cursor: pointer;
  position: relative;
  top: 0.001em;
  right: 1.6em;
  z-index: 3;
  color: #258900;
  font-size: 1.5em;
  line-height: 2.083em;
  &.disabled{
    cursor: not-allowed;
    pointer-events:none;
    color: @light_gray;
  }
}


